@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
* {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

html, body {
  min-height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  background-color: #8DAA9D;
  /*background: linear-gradient(0deg,  #F6F5F8 55%, #E3E3E3 65%);*/
}

body {
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  min-width: 100vw;
  align-content: center;
  justify-content: center;
  align-items: center;
}

.App-logo {
  max-height: 35vmin;
  max-width: 35vmin;
  pointer-events: none;
  text-align: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: #8DAA9D;
  padding: 8vmin;
}

.App-body {
  display: flex;
  flex-direction: column;
  width: 70%;
  align-content: space-evenly;
  justify-content: space-between;
  align-items: flex-start;
  padding: 3vmin;
}

.Socials {
  display: flex;
  justify-self: center;
  align-self: center;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.Socials img {
  height: 4vmin;
  display: flex;
  background: transparent;
  align-content: center;
  justify-content: center;
  border: none;
}

#email {
  font-size: 4vmin;
  align-self: flex-start;
  text-align: left;
}

.Socials a{
  display: flex;
  margin: 1vmin;
  color: black;
  text-decoration: none;
  background: transparent;
  box-shadow: none;
}

.App-body p {
  text-align: left;
  margin: 1vmin;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 800px) {
  .App-body {
    max-width: 80%;
  }
}
.Layout {
    display: flex;
    flex-direction: column;
    background-color: #FBF5F3;
    min-height: 100vh;
    width: 100%;
}
.Layout-body {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    width: 100%;
    height: 100%;
    justify-content: center;
    font-size: 2vh;
    color: black;
}

.Fill-Space {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}

.Layout-aside {
    position: fixed;
    margin: 6vmin .5vmin;
    bottom: 0;
    left: 0;
    max-width: 10%;
}

.Layout-header {
    background-color: #8DAA9D;
    width: 100vw; /* Full width */
}

.Layout-footer {
    display: flex;
    flex-direction: row;
    background-color: #FBF5F3;
    justify-content: center;
    height: 30px;
    width: 100%; /* Full width */
}

.Layout-link {
    color: #61dafb;
}

/* Add a black background color to the top navigation */
.topnav {
    background-color: inherit;
    overflow: hidden;
}

/* Style the links inside the navigation bar */
.topnav a {
    float: left;
    display: block;
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
}

/* Add an active class to highlight the current page */
.active {
    background-color: #4CAF50;
    color: white;
}

/* Hide the link that should open and close the topnav on small screens */
.topnav .icon {
    display: none;
}

/* Dropdown container - needed to position the dropdown content */
.dropdown {
    float: left;
    overflow: hidden;
}

/* Style the dropdown button to fit inside the topnav */
.dropdown .dropbtn {
    font-size: 17px;
    border: none;
    outline: none;
    color: black;
    padding: 14px 16px;
    background-color: inherit;
    font-family: inherit;
    margin: 0;
}

/* Style the dropdown content (hidden by default) */
.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

/* Style the links inside the dropdown */
.dropdown-content a {
    float: none;
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    text-align: left;
}

/* Add a dark background on topnav links and the dropdown button on hover */
.topnav a:hover, .dropdown:hover .dropbtn {
    background-color: #555;
    color: white;
}

/* Show the dropdown menu when the user moves the mouse over the dropdown button */
.dropdown:hover .dropdown-content {
    display: block;
}

.dropdown:hover .Layout-aside{
    visibility: hidden;
}

.Standout-text {
    font-weight: bolder;
}

.Section-title {
    display: flex;
    min-height: calc(150px + 7vh);
    height: 100%;
    width: 100vw;
    background-color: #8DAA9D;
    color: black;
    margin: 0 auto 7vh auto;
    font-size: calc(20px + 2vh);
    padding: 0;
    justify-content: center;
}

.Section-title h1 {
    margin: auto;
    text-align: center;
    text-shadow: 1.5px 1.5px lightgray;
}

@media screen and (max-width: 800px) {

    .Section-title {
        font-size: calc(20px + 1vh);
    }

    .topnav a:not(:first-child), .dropdown .dropbtn {
        display: none;
    }
    .topnav button.icon {
        float: right;
        text-align: left;
        cursor: pointer;
        text-decoration: none;
        border: none;
        padding: 2vh 4vh;
        background-color: inherit;
        display: block;
    }

    .topnav button.icon:focus {
        outline: 0;
    }

    .topnav button.icon:hover {
        background-color: #555;
        color: white;
    }

    .Layout-body {
        margin-bottom: 2vh;
    }

}

@media screen and (max-width: 800px) {
    .topnav.responsive {position: relative;}
    .topnav.responsive button.icon {
        position: absolute;
        right: 0;
        top: 0;
    }
    .topnav.responsive a {
        float: none;
        display: block;
        text-align: left;
    }
    .topnav.responsive .dropdown {float: none;}
    .topnav.responsive .dropdown-content {position: relative;}
    .topnav.responsive .dropdown .dropbtn {
        display: block;
        width: 100%;
        text-align: left;
    }
}



